.card-hover {
  box-shadow: $depth-box-shadow-1;

  @include transition($card-transition);

  &:hover:not(.disabled),
  &:focus:not(.disabled),
  &.active {
    box-shadow: $depth-box-shadow-8;
  }

  &.disabled {
    opacity: $card-disabled-opacity;
  }
}

.card-body-table {
  padding: .5rem 0;

  .table {
    margin-bottom: 0;
  }
}
